<template>
  <div class="settings-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item to="/layout"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
     </div>
       <el-row>
         <el-col :span="15">
          <el-form ref="form" :model="userInfo" :rules="ruleForm" class="setting-form" label-width="80px">
            <el-form-item label="">
              {{userInfo.id}}
            </el-form-item>
            <el-form-item label="">
              {{userInfo.mobile}}
            </el-form-item>
            <el-form-item prop="name" label="">
              <el-input v-model="userInfo.name"></el-input>
            </el-form-item>
            <el-form-item prop="intro" label="">
              <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}" v-model="userInfo.intro"></el-input>
            </el-form-item>
            <el-form-item prop="email" label="">
              <el-input v-model="userInfo.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" :loading="loadingUser" @click="onUpdateUser"></el-button>
            </el-form-item>
          </el-form>
         </el-col>
         <el-col :offset="2" style="text-align:center;" :span="4">
          <!-- <el-avatar shape="square" :size="150" fit="cover" :src="userInfo.photo"></el-avatar>
          <p @click="$refs.file.click()"></p> -->
          <label for="file">
            <el-avatar shape="square" :size="150" fit="cover" :src="userInfo.photo"></el-avatar>
            <p></p>
          </label>
          <input id="file" type="file" ref="file" hidden @change="onFileChange"/>
         </el-col>
       </el-row>
    </el-card>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      append-to-body
      @opened="onDialogOpen"
      @closed="onDialogClosed"
    >
      <div class="preview-image-wrap">
        <img class="preview-img" ref="pre-image" :src="previewImage" />
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" :loading="updatePhotoLoading" @click="onUpdate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getUserInfo, updateUserPhoto, updateUser } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utils/global-bus'
export default {
  name: 'SettingsIndex',
  data () {
    return {
      userInfo: {},
      dialogVisible: false,
      previewImage: '',
      cropper: null,
      updatePhotoLoading: false,
      ruleForm: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      loadingUser: false
    }
  },
  created () {
    this.loadUser()
  },
  methods: {
    loadUser () {
      getUserInfo().then(res => {
        this.userInfo = res.data.data
      })
    },
    onFileChange () {
      const file = this.$refs.file
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobData
      this.dialogVisible = true
      this.$refs.file.value = ''
    },
    onDialogOpen () {
      // replace 
      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return false
      }
      // img 
      // img  opened
      const image = this.$refs['pre-image']
      // 
      // .replace 
      // 
      this.cropper = new Cropper(image, {
        aspectRatio: 1,
        viewMode: 1,
        dragMode: 'none',
        cropBoxResizable: true
        // crop (event) {
        //   console.log(event.detail.x)
        //   console.log(event.detail.y)
        //   console.log(event.detail.width)
        //   console.log(event.detail.height)
        //   console.log(event.detail.rotate)
        //   console.log(event.detail.scaleX)
        //   console.log(event.detail.scaleY)
        // }
      })
    },
    onDialogClosed () {
      // 
      // this.cropper.destroy()
    },
    onUpdate () {
      this.updatePhotoLoading = true
      this.cropper.getCroppedCanvas().toBlob(file => {
        const fd = new FormData()
        fd.append('photo', file)
        updateUserPhoto(fd).then(res => {
          this.updatePhotoLoading = false
          this.dialogVisible = false
          this.userInfo.photo = window.URL.createObjectURL(file)
          this.$message({
            type: 'success',
            message: ''
          })
          // 
          // this.userInfo.photo = res.data.data.photo
          // this.loadUser()
          globalBus.$emit('update-user', this.userInfo)
        })
      })
    },
    onUpdateUser () {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.loadingUser = true
          updateUser(this.userInfo).then(res => {
            this.loadingUser = false
            this.$message({
              type: 'success',
              message: ''
            })
            // 
            globalBus.$emit('update-user', this.userInfo)
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="less">
.preview-image-wrap{
   .preview-img {
    display: block;
    max-width: 100%;
    height: 200px;
   }
}
</style>
